/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

.home,
.site-header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  // position: fixed;
  width: 100%;
  border-bottom: 1px solid #e1e1e1;

  .inhabitent-header {
    margin: 0 auto;
    display: flex;

    .front-logo img {
      height: 50px;
      width: 50px;
      padding-top: 5px;
    }

    .main-navigation {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-family: $font__novecento;
      text-transform: uppercase;

      a {
        color: #248a83;
      }

      .menu-item {
        padding: 0 12px;
      }
    }
  }
}


.front-hero-image {
  background-image: url(../../images/inhabitent-logo-full.svg),
    url(../../images/home-hero.jpg);
  background: linear-gradient(0, 0, 0, .4) 0%;
  background-repeat: no-repeat, no-repeat;
  background-size: auto, cover;
  background-position: center, center;
  height: 100vh;
}